import React from 'react'
import {ArrowRight} from '@styled-icons/bootstrap/ArrowRight'
import styled from 'styled-components'
import mobile from "./assets/mobile.svg"
import pc from "./assets/pc.svg"
import arvr from "./assets/arvr.svg"
import vrar from "./assets/vrar.svg"
import threeD from "./assets/3d.svg"
import ps4 from "./assets/ps4.svg"


export const Development = () => {
    const smallCard = (img, title) => (
            <Card>
                <Img src={img} alt="dasd"/>
                <P>{title}</P>
                <ArrowRight/>
            </Card>
    )
    
    
    return (
        <Container>
            <SubT>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</SubT>
            <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s.</Desc>
            <Wrapper>
                {smallCard(mobile, 'Mobile Game Development')}
                {smallCard(pc, 'PC Game Development')}
                {smallCard(ps4, 'PS4 Game Development')}
                {smallCard(arvr, 'AR/VR Solutions')}
                {smallCard(vrar, 'AR/VR design')}
                {smallCard(threeD, '3D Modelings')}
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    min-height: 100vh;
`
const SubT = styled.h2`
    margin: 300px auto 50px;
    max-width: 1200px;
    text-align: center;
    line-height: 2;
    font-size: var(--bf);
    font-weight: 600;
  
`
const Desc = styled.p`
    color: grey;
    line-height:2;
    max-width: 1200px;
    text-align: center;
    margin: 0 auto;
    font-size: var(--sf);
`
const Wrapper = styled.div`
    display: flex;
    flex-wrap: wrap;
    padding: 5vw;
    justify-content: center;
`
const Card = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 300px;
    justify-content: space-around;
    margin: 0 50px;
    >svg {
        height: 40px;
        color: var(--col);
        cursor: pointer;
    }
`
const Img = styled.img`
    width: 100px;
`
const P = styled.p`
    font-size: var(--sf);
    font-weight: 500;
`
