import React from 'react'
import styled from "styled-components"
import {NavLink} from "react-router-dom"
import { Phone } from '@styled-icons/icomoon/Phone'
import { LocationOn } from '@styled-icons/material'



export const Navbar = () => {
    return (
        <Container>
            <TopSide>
                <Info>
                    <LocationOn/>
                    Blvd. Eminescu, 304469
                </Info>
                <Info>
                    <Phone/>
                    +94 7477 7733 98
                </Info>
            </TopSide>
            <BottomSide>
                <Logo>LOGO</Logo>
                <LinksWrapper>
                    <MyNavLink to="/" exact={true} activeClassName="active">Home</MyNavLink>
                    <MyNavLink to="/about" activeClassName="active">About us</MyNavLink>
                    <MyNavLink to="/services" activeClassName="active">Services</MyNavLink>
                    <MyNavLink to="/news" activeClassName="active">News</MyNavLink>
                    <MyNavLink to="/contact" className="contact" activeClassName="active">Contact us</MyNavLink>
                </LinksWrapper>
            </BottomSide>
        </Container>
    )
}

const Container = styled.nav`

`
const TopSide = styled.div`
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
`
const Info = styled.small`
    display: flex;
    align-items: center;
    margin-right: 100px;
`
const BottomSide = styled.div`
    background-color: var(--con);
    display: flex;
    height: 70px;
    justify-content: space-between;
    align-items: center;
`
const Logo = styled.h2`
    margin-left: 100px;
    color: white;
`
const LinksWrapper = styled.div`
    margin-right: 100px;
    display: flex;
    align-items: center;
    gap: 5vw;
`
const MyNavLink = styled(NavLink)`
    color: white;
    text-decoration: none;
    &.contact {
        color: var(--col);
        background-color: white;
        border-radius: 5px;
        padding: 10px 30px;
        font-size: 0.9rem;
    }
    &.active {
        position: relative;
        &::after {
            position: absolute;
            content: "●";
            left: -20px;
            color: white;
            margin: auto 0;
        }
    }
`

