import React from 'react'
import styled from 'styled-components'
import img2 from "./assets/img2.png"


export const EndSection = () => {
    return (
        <Container>
            <Left>
                <Img src={img2} alt=""/>
            </Left>
            <Right>
                <Title>Lorem Ipsum is simply dummy text dummy text</Title>
                <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a gallery of type and scambled it to make a type specimen book. It has survived not only five centuries.</Desc>
                <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry</Desc>
                <Button>Read more</Button>
            </Right>
        </Container>
    )
}

const Container = styled.section`
    padding: 10vh 10vw 10vh 5vw;
    display: flex;
    align-items: center;
    min-height: 100vh;
    gap: 5vw;
`
const Left = styled.div`
    flex: 1;
`
const Img = styled.img`
    max-width: 100%;
`
const Right = styled.div`
    flex: 1;
`
const Title = styled.h2`
    font-size: var(--bf);
    margin-bottom: 2rem;
    line-height: 1.2;
    font-weight: 500;
`
const Desc = styled.p`
    color: grey;
    font-size: var(--sf);
    margin-bottom: 50px;
`
const Button = styled.button`
    background-color: var(--con);
    font-size: var(--sf);
    padding: 10px 30px;
    cursor: pointer;
    color: white;   
    border: none;
`
