import React, { useState } from 'react'
import styled from 'styled-components'
import {ArrowIosForwardOutline} from '@styled-icons/evaicons-outline/ArrowIosForwardOutline'
import {ArrowIosBackOutline} from '@styled-icons/evaicons-outline/ArrowIosBackOutline'
import {ArrowRight} from '@styled-icons/bootstrap/ArrowRight'

export const Slider = () => {
    const [slide, setSlide] = useState(0)
    
    const handleClick = (dir) => {
        if (dir === "left") {
            setSlide(slide > 0 ? slide - 1 : 6)
        } else {
            setSlide(slide < 6 ? slide + 1 : 0)
        }
    }

    const smallCard = (color) => (
        <Card>
            <CardSubT color={color}>Lorem Ipsum</CardSubT>
            <CardTitle>Lorem Ipsum</CardTitle>
            <CardDesc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s</CardDesc>
            <CardLink href="#">Read more <ArrowRight/></CardLink>
        </Card>
    )
    return (
        <Container>
            <Top>
                <Left>
                    <SubT>Lorem Ipsum</SubT>
                    <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry</Desc>
                </Left>
                <Right>
                    <Button onClick={() => handleClick("left")}><ArrowIosBackOutline/></Button>
                    <Button onClick={() => handleClick("right")}><ArrowIosForwardOutline/></Button>
                </Right>
            </Top>
            <Bottom slide={slide}>
                {smallCard("#ff0000")}
                {smallCard("#d0ff00")}
                {smallCard("#00ffff")}
                {smallCard("#ff7b00")}
                {smallCard("#ff00ea")}
                {smallCard("#3cff00")}
                {smallCard("#001aff")}
            </Bottom>
        </Container>
    )
}

const Container = styled.section`
    background-color: whitesmoke;
`
const Top = styled.div`
    padding: 10vh 10vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 1000px) {
        flex-direction: column;
    }
`
const Left = styled.div`
    max-width: 60vw;
`
const SubT = styled.h2`
    font-size: var(--xbf);
    margin-bottom: 2rem;
    font-weight: 600;
`
const Desc = styled.p`
    color: grey;
    line-height:2;
    max-width: 800px;
    font-size: var(--sf);
`
const Right = styled.div`

`
const Button = styled.button`
    border: none;
    background-color: white;
    border-radius: 50%;
    box-shadow: 0 0 15px lightgray;
    margin: 20px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    >svg {
        height: 30px;
        color: grey;
    }
    &:hover {
        box-shadow: 0 0 30px lightgray;
    }
`
const Bottom = styled.div`
    display: flex;
    padding-bottom: 100px;
    width: 3500px;
    overflow: hidden;
    transition: transform .5s ease-in-out;
    transform: translateX(${props => props.slide * -490}px);
    @media screen and (max-width: 500px) {
        transform: translateX(${props => props.slide * -390}px);
    }
`
const Card = styled.article`
    box-sizing: border-box;
    height: 400px;
    width: 450px;
    padding: 40px;
    background-color: white;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 20px;
    @media screen and (max-width: 500px) {
        width: 350px;
    }
`
const CardSubT = styled.h3`
    color: ${props => props.color};
    font-weight: 400;
    font-size: var(--xsf);
    background-color: ${props => props.color + "20"};
    padding: 10px 20px;
    border-radius: 10px;
    align-self: flex-start;
`
const CardTitle = styled.h2`

`
const CardDesc = styled.p`
    line-height: 1.7;
    color: #636363;
`
const CardLink = styled.a`
    align-self: flex-end;
    text-decoration: none;
    color: var(--cod);
    font-weight: 500;
`
