import React from 'react'
import styled from "styled-components"
import cover from "../components/assets/cover.png"
import illustration1 from "../components/assets/illustration1.svg"
import { Development } from '../components/Development'
import { EndSection } from '../components/EndSection'
import { Introduction } from '../components/Introduction'
import { Projects } from '../components/Projects'
import { Slider } from '../components/Slider'

export const Homepage = () => {
    return (
        <Container>
            <Wrapper>
                <Left>
                    <SubT>Digital Marketing</SubT>
                    <Title>Work that we produce for our clients</Title>
                    <Desc>Lorem ipsum is simply dummy text of the printing and typesetting industry. Lorem ipsum has been the industry's standard.</Desc>
                    <Button>Get more details</Button>
                </Left>
                <Right>
                    <Illustration src={illustration1} alt="illustration"/>
                </Right>
            </Wrapper>
            <Introduction />
            <Development/>
            <Slider/>
            <Projects/>
            <EndSection/>
        </Container>
    )
}


const Container = styled.main`

`
const Wrapper = styled.section`
    background: url(${cover}) bottom center/ cover;
    padding: 0 10vw;
    display: flex;
    align-items: center;
    min-height: calc(100vh - 100px);
`
const Left = styled.div`
    flex: 1;
`
const SubT = styled.h3`
    font-size: var(--sf);
    font-weight: 700;
    color: var(--col);
    margin-bottom: 1rem;
`
const Title = styled.h1`
    font-size: var(--xbf);
    font-weight: 700;
    margin-bottom: 2rem;
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 600;
    margin-bottom: 3rem;
    font-size: var(--sf);
`
const Button = styled.button`
    color: white;
    border: none;
    padding: 15px 30px;
    background-color: var(--col);
    border-radius: 30px;
    font-size: var(--xsf);
    cursor: pointer;
`
const Right = styled.div`
    
`
const Illustration = styled.img`
    max-width: 40vw;
    @media screen and (max-width: 1000px) {
        display: none;
    }
`

