import React from 'react'
import styled from 'styled-components'
import {Facebook} from '@styled-icons/boxicons-logos/Facebook'
import {Instagram} from '@styled-icons/boxicons-logos/Instagram'
import {Twitter} from '@styled-icons/boxicons-logos/Twitter'
import {Linkedin} from '@styled-icons/boxicons-logos/Linkedin'


export const Footer = () => {
    return (
        <Container>
            <Wrapper>
                <Side>
                    <Logo>LOGO</Logo>
                    <Desc margin="30px">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</Desc>
                    <Desc>@Company.com</Desc>
                </Side>
                <Side>
                    <SubT>About us</SubT>
                    <Link href="#">Company</Link>
                    <Link href="#">Portfolio</Link>
                    <Link href="#">Careers</Link>
                    <Link href="#">Contact us</Link>
                </Side>
                <Side>
                    <SubT>Contact us</SubT>
                    <Desc margin="50px">Lorem ipsum is simply dummy text of the printing and typesetting industry.</Desc>
                    <Desc>+94 7477 7733 98</Desc>
                </Side>
                <SideIcon>
                    <Icon><Facebook/></Icon>
                    <Icon><Instagram/></Icon>
                    <Icon><Twitter/></Icon>
                    <Icon><Linkedin/></Icon>
                </SideIcon>
            </Wrapper>
            <Bottom>
                <small>Copyright &copy;2021 Company All rights Reserved</small>
            </Bottom>
        </Container>
    )
}

const Container = styled.footer`
    background-color: #161616;
    color: white;
    display: flex;
    flex-direction: column;
`
const Wrapper = styled.div`
    display: flex;
    padding: 10vh 5vw;
    justify-content: space-between;
`
const Side = styled.div`
    max-width: 400px;
`
const Logo = styled.h2`
    font-size: var(--bf);
    margin-bottom: 2rem;
    line-height: 1.5;
`
const Desc = styled.p`
    margin-bottom: ${props => props.margin};
`
const SubT = styled.h4`
    margin-bottom: 30px;
`
const Link = styled.a`
    color: white;
    text-decoration: none;
    display: block;
    line-height: 2;
`
const SideIcon = styled.div`
    display: flex;
    align-items: flex-end;
    gap: 20px;
`
const Icon = styled.div`
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 50%;
    display: grid;
    place-content: center;
    cursor: pointer;
    >svg {
        color: black;
    }
`
const Bottom = styled.div`
    padding: 30px;
    border-top: 1px solid grey;
    text-align: center;
`
