import React from 'react'
import styled from 'styled-components'
import img1 from './assets/project1.png'
import img2 from './assets/project2.png'
import img3 from './assets/project3.png'
import img4 from './assets/project4.png'
import img5 from './assets/project5.png'
import img6 from './assets/project6.png'

export const Projects = () => {
    return (
        <Container>
            <SubT>Our Recent Projects</SubT>
            <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry</Desc>
            <Wrapper>
                <Img src={img1} alt="dd"/>
                <Img src={img2} alt="dd"/>
                <Img src={img3} alt="dd"/>
                <Img src={img4} alt="dd"/>
                <Img src={img5} alt="dd"/>
                <Img src={img6} alt="dd"/>
            </Wrapper>
        </Container>
    )
}


const Container = styled.section`
    min-height: 100vh;
    padding: 10vh 10vw;
`
const SubT = styled.h3`
    font-size: var(--bf);
    margin-bottom: 2rem;
    font-weight: 600;
    text-align: center;
    margin-top: 100px;
`
const Desc= styled.p`
    color: grey;
    line-height:2;
    max-width: 800px;
    font-size: var(--sf);
    text-align: center;
    margin: 0 auto 100px;
`
const Wrapper = styled.div`
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2vw;
`
const Img = styled.img`
    max-width: 80vw;
    overflow: hidden;
    transition: all .3s ease;
    &:hover {
        transform: scale(1.1);
    }
`

