import React from 'react'
import styled from 'styled-components'

export const Newsletter = () => {
    return (
        <Container>
            <SubT>Lorem Ipsum</SubT>
            <Desc color="grey">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</Desc>
            <Wrapper>
                <Left>
                    <SubT color="white">Stay in the loop</SubT>
                    <Desc color="white">Subscribe to receive the latest news and updates about TDA. We promise not to spam you!</Desc>
                </Left>
                <Right>
                    <Wrap>
                        <Input type="email" placeholder="Enter email address"/>
                        <Button>Continue</Button>
                    </Wrap>
                </Right>
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    padding: 0 10vw 20vh 10vw;
`
const SubT = styled.h3`
    font-size: var(--bf);
    margin-bottom: 2rem;
    font-weight: 600;
    color: ${props => props.color};
`
const Desc = styled.p`
    color: ${props => props.color};
    line-height:1.7;
    max-width: 800px;
    font-size: var(--sf);
`
const Wrapper = styled.div`
    background-color: #161616;
    border-radius: 20px;
    display: flex;
    align-items: center;
    padding: 30px 50px;
    margin-top: 50px;
`
const Left = styled.div`
    flex: 1;
`
const Right = styled.div`
    flex: 1;
`
const Wrap = styled.div`
    background-color: white;
    height: 50px;
    width: 80%;
    margin-left: auto;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
`
const Input = styled.input`
    height: 100%;
    border: none;
    padding-left: 50px;
    width: 100%;
`
const Button = styled.button`
    background-color: var(--col);
    border-radius: 10px;
    padding: 0 30px;
    color: white;
    margin: 5px;
    border: none;
    cursor: pointer;
`
