import React from 'react'
import styled from 'styled-components'
import img1 from './assets/img1.png'
import dots from './assets/dots.svg'

export const Introduction = () => {
    return (
        <Container>
            <SubT>Lorem Ipsum is simply dummy text of the printing and typesetting industry</SubT>
            <Wrapper>
                <Left>
                    <Title>Lorem Ipsum</Title>
                    <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500's</Desc>
                    <Img src={img1} alt="fsdf"/>
                </Left>
                <Right>
                    <Ill src={dots} alt="fsdf"/>
                </Right>
            </Wrapper>
        </Container>
    )
}


const Container = styled.section`
    min-height: 100vh;
`
const SubT = styled.h2`
    margin: 100px auto;
    max-width: 900px;
    text-align: center;
    line-height: 2;
    font-weight: 600;
    font-size: var(--bf);
    @media screen and (max-width: 5000px) {
        margin: 100px 50px;
    }
`
const Wrapper = styled.div`
    padding: 0 10vw;
    display: flex;
    align-items: center;
`
const Left = styled.div`
    flex: 1;
    position: relative;
    height: 600px;
`
const Title = styled.h1`
    font-size: var(--xbf);
    margin-bottom: 2rem;
`
const Desc = styled.p`
    color: grey;
    line-height:2;
    max-width: 40vw;
    font-size: var(--sf);
`
const Img = styled.img`
    position: absolute;
    top: 300px;
    left: 0;
    width: 60vw;
`
const Right = styled.div`
    flex: 1;
    display: grid;
    place-content: center;
    height: 100%;
`
const Ill = styled.img`

`